
import 'chartjs-adapter-moment';

import Modal from "@/components/common/Modal.vue";

import {defineComponent} from "vue";
import moment, {Moment} from "moment";
import Remote from "@/remote/scheduleRequest";
import {EmployeeGetters} from "@/store/employeeModule";

declare var Affinity: any

export interface CostDay {
    scheduled: number;
    budgeted: number;
    sales: number;
    date: string;
    weekDay: string;
    budgetedVSScheduled: number;
    budgetedVSSales: number;
    salesVSScheduled: number;
}

export default defineComponent ({
    name: "CostAnalysisDialog",
    components: {
        Modal,
    },
    props: {
        dayFocus: {
            type: Object
        },
        modalIndex: {
            type: Number
        }
    },
    data() {
        return {
            selectedDate: <Moment | null> null,
            data: <any[]>[],
            total: <any>{},
            displayN: 6, // number of column to display,
            displayList: {
                scheduled: false,
                budgeted: false,
                sales: false,
                budgetedVSSales: false,
                budgetedVSScheduled: false,
                salesVSScheduled: false,
            }
        }
    },

    setup() {
        //
    },

    methods: {

        open() {
            (this.$refs.ModalRef as any).toggleShow();

            this.displayList = {
                scheduled: false,
                budgeted: false,
                sales: false,
                budgetedVSSales: false,
                budgetedVSScheduled: false,
                salesVSScheduled: false,
            }

            let weekStart = (this.dayFocus as any).clone().startOf('week').format("DD/MMM/YYYY");
            let weekEnd =  (this.dayFocus as any).clone().endOf('week').format("DD/MMM/YYYY");
            let employeeNumbers = this.$store.getters[EmployeeGetters.getEmployees].map((emp: any) => {
                return emp.employeeNo
            }) ;

            let data = {
                employeeNumbers: employeeNumbers,
                // employeeNumbers: [1060],
                dateFrom: weekStart,
                dateTo: weekEnd
            }

            // let data = {
            //     employeeNumbers: [1060],
            //     dateFrom: "03/Jan/2021",
            //     dateTo: "09/Jan/2021"
            // }

            Remote.GetCostAnalysis(data).then(res => {
                if (!res[0]) {
                    return;
                }

                let costDays = res.map((element: any) => {
                    return Object.fromEntries(
                        Object.entries(element).map(([k,v]) => [k.charAt(0).toLowerCase() + k.slice(1), v])
                    );
                })

                this.calculateDisplayRow(costDays);

                this.data = costDays.map((costDay: CostDay) => {
                    let newDay: CostDay = {...costDay};

                    newDay.weekDay = moment(costDay.date).format('dddd');

                    if (costDay.budgeted && costDay.scheduled) {
                        newDay.budgetedVSScheduled = Math.floor(costDay.budgeted / costDay.scheduled * 100)
                    }

                    if (costDay.budgeted && costDay.sales) {
                        newDay.budgetedVSSales = Math.floor(costDay.budgeted / costDay.sales * 100)
                    }
                    if (costDay.sales && costDay.scheduled) {
                        newDay.salesVSScheduled = Math.floor(costDay.sales / costDay.scheduled * 100)
                    }

                    return newDay;
                })

                this.calculateTotal();

            })


        },

        calculateTotal() {
            const keyList = [
                'scheduled',
                'budgeted',
                'sales',
              ]

            for (let k of keyList) {
                if (this.data[0][k] === 0) {
                    if (this.data[1] && this.data[1][k] === 0) {
                        continue;
                    }
                }

                this.total[k] = this.data.reduce((pre: any, next: any) => {
                    if (typeof pre === 'object') {
                        return pre[k] + next[k]
                    }
                    return pre + next[k]
                });
            }

            if (this.total.budgeted && this.total.scheduled) {
                this.total.budgetedVSScheduled = Math.floor(this.total.budgeted / this.total.scheduled * 100)
            }

            if (this.total.budgeted && this.total.sales) {
                this.total.budgetedVSSales = Math.floor(this.total.budgeted / this.total.sales * 100)
            }
            if (this.total.sales && this.total.scheduled) {
                this.total.salesVSScheduled = Math.floor(this.total.sales / this.total.scheduled * 100)
            }
        },

        calculateDisplayRow(days: CostDay[]) {
            console.log('day', days)
            let displayDef = 1;
            for (let day of days) {
                let displayN = 1;

                if (day.scheduled) {
                    this.displayList.scheduled = true;
                }

                if (day.budgeted) {
                    this.displayList.budgeted = true;
                }

                if (day.sales) {
                    this.displayList.sales = true;
                }

                if (day.budgeted && day.sales) {
                    this.displayList.budgetedVSSales = true;
                }

                if (day.scheduled && day.sales) {
                    this.displayList.salesVSScheduled = true;
                }

                if (day.budgeted && day.scheduled) {
                    this.displayList.budgetedVSScheduled = true;
                }

                if (day.budgeted === 0 && day.sales === 0) {
                    displayN = 1;
                } else if (day.budgeted == 0 && day.sales !== 0) {
                    displayN = 3;
                } else if (day.budgeted !== 0 && day.sales == 0) {
                    displayN = 3;
                } else if (day.budgeted && day.sales && day.scheduled) {
                    displayN = 6;
                }

                if (displayN > displayDef) {
                    displayDef = displayN;
                }
                console.log('display', displayDef)
            }

            this.displayN = displayDef;
        },

        toggleShow() {
            (this.$refs.ModalRef as any).toggleShow();
        },


        cancelModal() {

        }
    },
})
